/* *
 *评论页面
 * */
import util,{ parGetData, parSetData, checklogin } from '../../common/util';
export default {
	data() {
		return {
			data: {},
			detailDec: '',
			text: '',
			id: '',
			showlogin: false,
			replylist: [],
			fetchPageNum: 1,
			openid: '',
			token: '',
			userinfo: '',
			loading: true
		};
	},
	onShow() {
		this.checkstat();
		this.fetchPageNum = 1;
		this.getData(this.id);
	},
	onLoad(e) {
		// e.data为获取的字符串
		// data 为传递来的数据
		this.id = e.newsid
		//比起使用vuex，更适合用uni.getStorage
		this.data = uni.getStorageSync('detail')
		console.log(this.data)
		this.data.c = this.data.c.replace(/<img/g, '<img width="100%"')
		this.loading = false
	
		
	},
	onShareAppMessage() {
		return {
			title: this.detailDec ? this.detailDec : '最新最好笑的趣事趣图，来看下吧',
			path: '/pages/detail/detail?id=' + this.id
		};
	},
	methods: {
		checkstat() {
			var logininfo = util.checklogin()
			this.openid = logininfo.openid;
			this.userinfo = logininfo.userinfo;
			this.token = logininfo.token;
		},
		checklogin() {
			if (!this.token) {
		
				uni.showModal({
					title: '请登录',
					content: '请登录后继续操作',
					confirmText: '去登录',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
							
								url: '/pages/login/login'
							})						
						}
					}
				});
				
				this.showlogin = true;
				return false;
			}
			
		},
		send(e) {		
			if (!this.text) {
				uni.showToast({
					title: '请输入评论内容',
					icon: 'none'
				});
				return false;
			}
			const self = this;
			self.replylist.unshift({
				text: this.text,
				avatar: this.userinfo.avatar,
				nick: this.userinfo.ddusername,
				pub_time: '刚刚'
			});
			uni.request({
				url: util.apiurl + 'api/v1/post_comment',
				method: 'POST',
				data: {
					text: self.text,
					id: self.id,
					token: self.token,
					shebei: util.shebei
				},
				success: ret => {
					
					if (ret.statusCode == 200 && ret.data.code == 1) {
						self.text = '';
						uni.showToast({
							title: '审核后显示'
						});
						setTimeout(function() {
							
						}, 1000);
					} else {
						uni.showToast({
							title: ret.data.msg,
							icon: 'none'
						});
						setTimeout(function() {
							
						}, 1000);
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					this.loading = false;
				},
				header: {
					Authorization: util.httpauth
				}
			});
		},
		setlike(type, index, tid) {
			alert(1024)
		},
		onReachBottom() {
			/* 到底部加载 */
			this.getData(this.id);
		},
		getData(id) {
			const self = this;
			self.loading = true;
			// 获取评论
			uni.request({
				url: util.apiurl + 'api/v1/get_comment',
				method: 'get',
				dataType: 'json',
				data: {
					shebei: util.shebei
				},
				success: ret => {
					self.loading = false;
					if (ret.statusCode !== 200) {
						uni.showToast({
							title: '请求数据失败请重试' + ret.statusCode,
							duration: 2000,
							icon: 'none'
						});
					} else {
						if (self.fetchPageNum < 2) {
							// 这是刚进入时第一页
						} else if (ret.data.length < 1) {
							// 大于第一页且无评论
							uni.showToast({
								title: '没有更多评论了',
								icon: 'none'
							});
							return false;
						}
						// 第一页或者大于第一页且有评论时到这里
						self.replylist = ret.data;					
						self.fetchPageNum += 1;
					}
				},
				fail: res => {
					uni.showToast({
						title: '请求数据失败请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					self.loading = false;
				},
				header: {
					Authorization: util.httpauth
				}
			});
		},
		
		/* 登录方法 */
		login(res) {
			util.logincomm(res, this);
		},
		
	}
};
